  import React, { useState } from 'react'
  import { Document, Page, pdfjs} from 'react-pdf';
  import styled from 'styled-components'

  import { SizeMe } from 'react-sizeme'

  import GlobalWrapper from 'components/global-wrapper'
  import media from 'utils/media-queries'
  import Link from 'gatsby-link'


  import Header from 'components/article/header'
  import CoverImage from 'components/article/coverimage'
  import ContentWrapper from 'components/article/contentwrapper'
  import Footer from 'components/footer'
  import FullSizeImage from 'components/article/fullsizeimage'
  import LargeImage from 'components/article/largeimage'

  import CoverSrc from 'img/articles/comp/dria/00_designing_res_intro.png'
  import ContentPDF from 'img/articles/comp/dria/01_dria_building_with_nature.pdf'

  pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`;

  const Section = styled.section`
    margin: 64px 0;
    ${media.sm`
      margin: 0;
    `}
  `

  const Content = styled.div`
    canvas {
      width: 100% !important;
      height: auto !important;
    }
    max-width: 100%;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    p + h3 {
      margin-top: 42px;
    }
    padding: 0 0 0 0;
    ${media.lg`
      padding-right: 0;
    `}
    ${media.xs`
    max-width: 700px;
    padding: 0 0 0 0;
    `}
  `

  const EmbedContainer = styled.div`
    position: relative;
    padding-bottom: 56.25%;
    height: 0;
    overflow: hidden;
    width: 100%;
    height: auto;
    margin-top: 32px;
    margin-bottom: 32px;
    @media (min-width: 1000px) {
      width: 1000px;
      height: 562px;
      padding-bottom: 0px;
      margin-left: auto;
      margin-right: auto;
    }
  `

  const Video = styled.iframe`
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  `

  const ImageGrid = styled.div`
    background: #f2f2f2;
    max-width: 100%;
    padding: 40px;
    margin: 32px 0;
    text-align: center;
    ${media.lg`
      padding: 32px;
      margin: 0 0 24px 0;
    `}
    ${media.md`
      margin: 0;
      margin: 0 0 16px 0;
    `}
    ${media.sm`
      padding: 16px;
    `}
  `

  const Img = styled.img`
    max-width: calc(100% - 96px);
    max-height: 300px;
    margin: 8px;
    display: inline-block;
    ${media.xl`
      max-height: 240px;
    `}
    ${media.lg`
      max-height: 200px;
    `}
    ${media.md`
      max-height: 120px;
      margin: 4px;
    `}
    ${media.sm`
      max-height: 80px;
      margin: 2px;
    `}
  `

  const LifeTime = () => {
    const [numPages, setNumPages] = useState(null);
    return (
      <GlobalWrapper>
        <Header title="Designing resilience in Asia" lead="Strengthening Water Ecosystems -  Building with nature" />
        <CoverImage src={CoverSrc} focusX={'20%'} focusY={'100%'} />
        <Section>
          <ContentWrapper>
            <h2>Designing resilience in Asia </h2>
            <p><b>Team //</b> Chandra Bhanu,  Ekta Rakholiya</p>
            <p><i>Honorable mention</i></p>
            <p>Visit <a target="_blank"  href="http://designingresilience.com/proposals/strengthening-w-e-water-ecosystems">Link</a></p>
            <p>
            The Designing Resilience in Asia (DRIA) is an international research program on urban 
            and architecture resilience, initiated by the School of Design and Environment of the 
            National University of Singapore (NUS). Aim of the program is to promote and foster 
            substantive research and discourse around innovative ideas for resilient Asian cities. 
            Leading research organisations, professors and students from all over the world are invited 
            to contribute to the debate on how to make Asian cities resilient in face of Climate change.
            </p>
            <p>
            As student participants for the DRIA 2017 competition, <b>our proposal aims to 
            re-establish the coastline</b> of the highly flood prone city Semarang (Indonesia) by building with nature.  
            </p>
            <p>
            Semarang has been struggling to cope with climate change since decades. 
            It is predicted that the distressed situations experienced in the coastal 
            region can soon be reality in the core city areas. As part of the long term vision 
            of the proposed plan, it serves as a prototype that <b>can be replicated and adapted 
            to the other regions of Semarang</b> as flood water penetrates new areas of the city.
            </p>
            <p>
            The project title “Strengthening W.E.(Water Ecosystem), Building with Nature” refers 
            to developing and maintaining healthy coastal and river ecosystems through prolonged 
            interactions between human beings and nature. Hence, WE - the people, unite together 
            to develop and rejuvenate water ecosystems and adapt to a new kind of resiliency provided by nature. 
            </p>
          </ContentWrapper>
        </Section>
        <Content>
        <SizeMe
          monitorHeight
          refreshRate={128}
          refreshMode={"debounce"}
          render={({ size }) => (
          <div>
            <Document 
              file={ContentPDF} 
              onLoadSuccess={({ numPages })=>setNumPages(numPages)}>
              {Array.apply(null, Array(numPages))
                  .map((x, i)=>i+1)
                  .map(page => <Page pageNumber={page} width={size.width} renderMode="png" scale={1.0}/>)}
            </Document>
          </div>
        )} />
        </Content>
        <Footer />
      </GlobalWrapper>
    )
  }

  export default LifeTime
